.layout{
    margin: 0 100px;
    
  
}
.header{
    background-color:#142d4c ;
    padding: 15px;
    border-radius: 7px;
    margin-top: 3px;
   
}
.logo{
    font-size: 30px;
    color: #bfcee0;
    margin: 0 !important;
    cursor: pointer;
}
.username{
    font-size: 18px;
    color: rgba(255, 255, 255, 0.742);
}


.content{
    height: 85vh;
    box-shadow: 0 0 2px gray;
    margin-top: 20px;
    border-radius: 5px;
    /* border-top-left-radius: 25px; */
    padding: 15px;
    /* overflow-y: scroll; */
    overflow-y: auto;
}

.spinner{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50% , -50%);
}

.ant-spin-dot-item{
    background-color: gray !important;
}

@media screen and (max-width:700px){
    .layout{
        margin: 0 15px;
    }
}

